export default {
  props: {
    voucher: {
      type: Object,
      required: true,
    },

    use_custom_voucher: {
      type: Function,
      required: true,
    },

    submit: {
      type: Function,
      required: true,
    },
  },

  data: () => ({
    select_voucher: false,
    voucher_code: '',
  }),

  computed: {
    site() {
      return this.$store.state.site
    },
  },

  methods: {
    use_voucher(status, params) {
      this.select_voucher = false

      if (!status && !params) {
        this.use_custom_voucher()
      } else {
        this.submit(status, params)
      }
    }
  }
}
